import classNames from 'classnames';
import { FC, useEffect, useState } from 'react';
import { CartProduct } from '../types/CartProduct';

type Props = {
  cartItem: CartProduct;
  updateCount: (id: string, itemCount: number) => void;
  deleteItem: (id: string) => void;
};

export const CartItem: FC<Props> = ({
  cartItem,
  updateCount,
  deleteItem,
}) => {
  const [itemCount, setItemCount] = useState(+cartItem.count);

  useEffect(() => {
    updateCount(cartItem.item.id, itemCount);
  }, [itemCount]);

  const addCount = () => {
    setItemCount(itemCount + 1);
  };

  const removeCount = () => {
    if (itemCount > 1) {
      setItemCount(itemCount - 1);
    }
  };

  return (
    <div className="cart__item">
      <button
        type="button"
        data-cy="cartDeleteButton"
        className="cart__item-delete-btn"
        onClick={() => deleteItem(cartItem.item.id)}
      >
        x
      </button>
      <img
        src={cartItem.item.imageUrl}
        alt="#"
        className="cart__item-image"
      />
      <h2 className="cart__item-title">{cartItem.item.name}</h2>
      <div className="cart__item-counter">
        <button
          type="button"
          className={classNames('cart__item-count-btn', {
            'cart__item-count-btn--active': itemCount > 1,
          })}
          onClick={() => removeCount()}
        >
          -
        </button>
        {itemCount}
        <button
          type="button"
          className="cart__item-count-btn cart__item-count-btn--active"
          onClick={() => addCount()}
        >
          +
        </button>
        <p className="cart__item-price">
          {`$${cartItem.item.price - ((cartItem.item.price / 100) * cartItem.item.discount)}`}
        </p>
      </div>
    </div>
  );
};
